
.notifications{padding-bottom:15em
}
.notifications .panel-heading{position:relative
}
.notifications .panel-heading .read-button{position:absolute;right:0.7em;height:1.8em;line-height:100%
}
.notifications .unseen-count{display:inline-block;background-color:rgba(255,16,8,0.8);text-shadow:0px 0px 3px rgba(0,0,0,0.5);min-width:1.3em;border-radius:1.3em;margin:0 0.2em 0 -0.4em;color:white;font-size:0.9em;text-align:center;line-height:1.3em
}
.notifications .notification{padding:0.4em 0 0 10px;display:-ms-flexbox;display:flex;border-bottom:1px solid;border-bottom-color:inherit
}
.notifications .notification .text{min-width:0px;word-wrap:break-word;line-height:18px;padding:0.3em 0.8em 0.5em
}
.notifications .notification .text .icon-retweet.lit{color:#0fa00f
}
.notifications .notification .text .icon-reply.lit{color:#0095ff
}
.notifications .notification .text h1{word-break:break-all;margin:0 0 0.3em;padding:0;font-size:1em;line-height:20px
}
.notifications .notification .text h1 small{font-weight:lighter
}
.notifications .notification .text p{margin:0;margin-top:0;margin-bottom:0.3em
}
.notifications .notification .avatar{padding-top:0.3em;width:32px;height:32px;border-radius:50%
}
.notifications .notification:last-child{border-bottom:none;border-radius:0 0 10px 10px
}
.notifications .unseen{border-left:4px solid rgba(255,16,8,0.75);padding-left:6px
}



