
.nav-panel ul{list-style:none;margin:0;padding:0
}
.nav-panel li{border-bottom:1px solid;border-color:inherit;padding:0
}
.nav-panel li:first-child a{border-top-right-radius:10px;border-top-left-radius:10px
}
.nav-panel li:last-child a{border-bottom-right-radius:10px;border-bottom-left-radius:10px
}
.nav-panel li:last-child{border:none
}
.nav-panel a{display:block;padding:0.8em 0.85em
}
.nav-panel a:hover{background-color:transparent
}
.nav-panel a.router-link-active{font-weight:bolder;background-color:transparent
}
.nav-panel a.router-link-active:hover{text-decoration:underline
}



