
.timeline .timeline-heading{position:relative;display:-ms-flexbox;display:flex
}
.timeline .title{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;max-width:70%
}
.timeline .loadmore-button{position:absolute;right:0.6em;font-size:14px;min-width:6em;height:1.8em;line-height:100%
}
.timeline .loadmore-text{position:absolute;right:0.6em;font-size:14px;min-width:6em;border-radius:5px;font-family:sans-serif;text-align:center;padding:0 0.5em 0 0.5em;opacity:0.8
}
.timeline .error{background-color:rgba(255,48,16,0.65)
}
.new-status-notification{position:relative;margin-top:-1px;font-size:1.1em;border-width:1px 0 0 0;border-style:solid;border-radius:0 0 10px 10px;padding:10px;z-index:1
}



