
#app{background-size:cover;background-attachment:fixed;background-repeat:no-repeat;background-position:0 50px;min-height:100vh
}
h4{margin:0
}
#content{padding-top:60px
}
.text-center{text-align:center
}
body{font-family:sans-serif;font-size:14px;margin:0
}
a{text-decoration:none
}
button{border:none;border-radius:5px;cursor:pointer;border-top:1px solid rgba(255,255,255,0.2);border-bottom:1px solid rgba(0,0,0,0.2);box-shadow:0px 0px 2px black;font-size:14px;font-family:sans-serif
}
button:hover{box-shadow:0px 0px 4px rgba(255,255,255,0.3)
}
.container{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:0;padding:0 10px 0 10px
}
.gaps{margin:-1em 0 0 -1em
}
.item{-ms-flex:1;flex:1;line-height:21px;height:21px
}
.gaps>.item{padding:1em 0 0 1em
}
.auto-size{-ms-flex:1;flex:1
}
nav{width:100%;-ms-flex-align:center;align-items:center;position:fixed;height:50px
}
nav .inner-nav{padding-left:20px;padding-right:20px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-preferred-size:970px;flex-basis:970px;margin:auto;height:50px;background-repeat:no-repeat;background-position:center;background-size:contain
}
main-router{-ms-flex:1;flex:1
}
.status.compact{color:rgba(0,0,0,0.42);font-weight:300
}
.status.compact p{margin:0;font-size:0.8em
}
.panel{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin:0.5em;border-radius:10px
}
.panel-heading{border-radius:10px 10px 0 0;background-size:cover;padding:0.6em 1.0em;text-align:left;font-size:1.3em;line-height:24px
}
.panel-footer{border-radius:0 0 10px 10px
}
.panel-body>p{line-height:18px;padding:1em;margin:0
}
#content{margin:auto;max-width:980px;border-radius:10px;padding-bottom:1em;background-color:rgba(0,0,0,0.1)
}
.container>*{min-width:0px
}
.fa{color:grey
}
nav{z-index:1000
}
.fade-enter-active,.fade-leave-active{transition:opacity .2s
}
.fade-enter,.fade-leave-active{opacity:0
}
.main{-ms-flex-preferred-size:60%;flex-basis:60%;-ms-flex-positive:1;flex-grow:1;-ms-flex-negative:1;flex-shrink:1
}
.sidebar{-ms-flex:0;flex:0;-ms-flex-preferred-size:35%;flex-basis:35%
}
.sidebar-flexer{-ms-flex:1;flex:1;-ms-flex-preferred-size:345px;flex-basis:345px;width:365px
}
.mobile-shown{display:none
}
.panel-switcher{display:none;width:100%
}
.panel-switcher button{display:block;-ms-flex:1;flex:1;margin:0.5em;padding:0.5em
}
@media all and (min-width: 960px){
.sidebar{overflow:hidden;max-height:100vh;width:350px;position:fixed;margin-top:-10px
}
.sidebar .sidebar-container{height:96vh;width:362px;padding-top:10px;padding-right:20px;overflow-x:hidden;overflow-y:scroll
}
.sidebar-flexer{max-height:96vh;-ms-flex-negative:0;flex-shrink:0;-ms-flex-positive:0;flex-grow:0
}
}
@media all and (max-width: 959px){
.mobile-hidden{display:none
}
.panel-switcher{display:-ms-flexbox;display:flex
}
.container{padding:0 0 0 0
}
.panel{margin:0.5em 0 0.5em 0
}
}
.item.right{text-align:right;padding-right:20px
}



