
.conversation-heading{border-bottom-style:solid;border-bottom-width:1px
}
.status-preview{position:absolute;max-width:35em;padding:0.5em;display:-ms-flexbox;display:flex;border-color:inherit;border-style:solid;border-width:1px;border-radius:4px;box-shadow:2px 2px 3px rgba(0,0,0,0.5)
}
.status-preview .avatar{width:32px;height:32px;border-radius:50%
}
.status-preview .text{padding:0 0.5em 0.5em 0.5em
}
.status-preview .text h4{margin-bottom:0.4em
}
.status-preview .text h4 small{font-weight:lighter
}



